.home{
    position: relative;
    height: 100%;
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;


     &-text-wrapper{
    display: flex;
    justify-content: center;
    align-content: center;
    padding-top: 10%;
      h1{
        color: var(--yellow-theme-main-color );
        margin: 0px;
        font-weight: 500;
        font-size: 8rem;
        line-height: 10rem;
        position: relative;
        overflow: hidden;
        white-space: nowrap;
        animation: typingEffectAnimation 2.5s steps(30,end);
       }
   }
}
.home-contact-me{
    display: flex;
    justify-content:center;
    margin-top: 15px;

    button{
        cursor: pointer;
        padding: 10px 70px ;
        font-size: 2rem;
        background: transparent;
        color: var(--yellow-theme-main-color);
        border: 1.5px solid var(--yellow-theme-main-color);
        border-radius: 4px;
        font-weight: 500;

        &:hover{
            background: var(--yellow-theme-main-color);
            color: black;
        }
    }
}
.home-contact-me{
    display: flex;
    justify-content: center;
    margin-top: 15px;
    button{
        &:not(:last-child){
            margin-right: 10px;
        }
    }
}
@media only screen and (max-width:550px){
    .home-text-wrapper h1{
        font-size: 3rem;
        line-height: 5rem;

    }
}
@media only screen and (min-width:551px) and (max-width:768px){
    .home-text-wrapper h1{
        font-size: 4rem;
        line-height: 6rem;

    }
}
@media only screen and (min-width:769px) and (max-width:990px){
    .home-text-wrapper h1{
        font-size: 6rem;
        line-height: 8rem;

    }
}

@keyframes  typingEffectAnimation{
    from{
        width: 0;
    }
    to{
        width: 100%;
    }
}